body {
  background-color: #FCFEFC;
  margin: 0;
}

.valentine-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}


.text {
  font-size: 24px;
  margin: 20px 0;
  font-weight: bold;
  color: #333;
}

.yesBtn {
  background-color: #2dac75;
  color: white;
  margin: 8px;
  padding: 1rem;
  border: white;
  border-radius: 1rem;
  font-size: 16px;
  cursor: pointer;
}

.noBtn {
  background-color: #dd3162;
  color: white;
  margin: 8px;
  padding: 1rem;
  border: white;
  border-radius: 1rem;
  font-size: 16px;
  cursor: pointer;
}

@media (max-width: 600px) {
  .valentine-container {
    padding: 10px; 
  }

  .text {
    font-size: 18px; 
    margin: 10px 0; 
  }

  .yesBtn, .noBtn {
    font-size: 14px;
  }

  img {
    max-width: 100%;
    height: auto;
  }

}